import React from 'react';
import './Contact.css';
import FormContact from './FormContact';

const Contact = () => {
  return (
    <div>
     <div className='contact container'>
      <div className='c-contact'>
       <h2>How can we help you?</h2>
       <p>If you would like to know more
        about how you can enjoy your dashboard
        on the web, please contact us and we will
        be in touch.</p>
        <div>
         <div className='c-icons'>
          <div>
           <img src={require("../../images/2055752009.jpg")} alt='shape1' />
           <span>Visit us</span>
           <p>Jeddah, Saudi Arabia</p>
          </div>
           <div>
            <div>
             <img src={require("../../images/11 - 20.jpg")} alt='shape1' />
             <span>Call us</span>
            <p>+966 055 021 0280</p>
          </div>
         </div>
         <div>
         <div>
          <img src={require("../../images/Message.jpg")} alt='shape1' />
          <span>Talk to us</span> 
          <p>support@postgait.com</p>
         </div>
        </div>
        <div>
         <div>
          <img src={require("../../images/12431494641536559839 1.jpg")} alt='shape1' />
           <span>Working hours</span>
          <p>Sunday-Thursday: 9 am — 6 pm</p>
         </div>
        </div>
         </div>
        </div>
      </div>
      <div>
       <FormContact />
      </div>
     </div>
    </div>
  )
}

export default Contact
