import React from 'react';
import Cards from './card/Card';

const Blog = () => {
  return (
    <div>
     <div className='blog'>
      <h2>Welcome to BLOG</h2>
      <p>We Provide Every New, Postgait Seek To Help You By Providing Explanations About Our Services Or Everything You Need To Know..</p>
     </div>
      <div className='description'>
       <div className='c-containers'>
        <div className='c-rectangle'>

         <div className='b-scearch'>
          <input type='scearch' placeholder='Find an article' />
          <i class="fa-solid fa-magnifying-glass"></i>
         </div>

         <div>
          <Cards />
         </div>
        
        </div>
      </div>
     </div>
    </div>
  )
}

export default Blog;