import React, { Component } from 'react';
import './Navbar.css';
import { Link } from 'react-router-dom';

class Navbar extends Component {
  state = {clicked: false};
  handleclick = () => {
    this.setState({clicked: !this.state.clicked})
  }

render() {
  return (
    <div className='n-navbars' >
      <nav>
       <div className='logo'>
        <Link to='/'>
         <img src={require("../../../images/preview.png")} alt='shape1' />
        </Link>
       </div>
       <div className='n-nav'>
        <ul id='navbar' className={this.state.clicked ? "#navbar active" : "#navbar"}>
         <div className='n-li'>
          <li><Link to='/' className='active'>Home</Link></li>
          <li><Link to='/about'>About Us</Link></li>
          <li><Link to='/contact'>Contact Us</Link></li>
          <li><Link to='/blog'>Blog</Link></li>
         </div>
         <div className='n-li n-li-n'>
          <li><Link to='#' className='lang'>العربية</Link></li>
          <li><Link to='/login'>Login</Link></li>
          <li><Link to='#' className='n-btn'>Start For Free</Link></li>
         </div>
       </ul>
      </div>
      <div id='mobile' onClick={this.handleclick}>
           <i id='bar' className={this.state.clicked ? "fa-solid fa-xmark fa-flip"
           : "fa-solid fa-bars-staggered fa-flip" }></i>    
      </div>
      </nav>
    </div>
  )
 }
}
export default Navbar;