import React from 'react';
import './Header.css';

const Header = () => {
  return (
    <div className='header'>
     <div className='h-center'>
      <h1>THE FASTEST SHIPPING PLATFORM FOR ONLINE STORES</h1>
      <p>Postgait It is the most appropriate and safest choice for premium shipping</p>
      <div className='h-namber'>
       <div className='barth'>
        <h2>99.8%</h2>
        <span>Success rate</span>
       </div>
       <div className='barth'>
        <h2>90+</h2>
        <span>Client</span>
       </div>
       <div className='barth'>
        <h2>120K</h2>
        <span>Policy</span>
       </div>
      </div>
      <div className='h-btn'>
       <input className='submit' type='submit' name='' value='Start charging now' />
       <i className="fa-solid fa-truck-fast"></i>
      </div>
     </div>
    </div>
  )
}

export default Header;
