import React, { useState } from 'react';
import './Headers.css';
import { Link } from 'react-router-dom';



const Headers = ({Children}) => {
  const [click , setClick] = useState(true);
  const [product, setProduct] = useState(false);
  const [product2, setProduct2] = useState(false);
  const [product3, setProduct3] = useState(false);

  const handelClick = () => setClick(!click)
  const closeMobileMenu = () =>setClick(false)


  return (
    <navbar className='nav-header' style={{width: click ? "270px" : "80px"}}>
      <ul >

       <div className='logos' style={{width: click ? "270px" : "80px"}}>
       <div className='menu-icon'>
        <i onClick={handelClick} className={click ? 'fas fa-times' : 'fas fa-bars'}></i>
       </div>
       <Link to='/'>
        <img className='img-logo' style={{display: click ? "block" : "none"}} src={require("../../../images/presview.png")} alt='shape1' />
       </Link>
       </div>

       <div>
       <div className='n-center'>
       <div>

       <Link to='/dashboard' name="Dashboard" className='nav-links'>
        <li className='n-main active'>
         <i className="fa-solid fa-house"></i> 
         <span  style={{display: click ? "block" : "none"}}>Dashboard</span>
         </li>
        </Link>

        <li className='n-main n-left nav-item'>
        <Link to='/shops' style={{color: "#144339"}}>
          <div className='main-icone'>
           <i className="fa-solid fa-shop"></i>
           <span  style={{display: click ? "block" : "none"}}> Shops</span>
          </div>
          </Link>
      { product && <ul className="dropDown">
           <li className="dropDownPath"><Link to="/products">Product</Link></li>
           <li className="dropDownPath"><Link to="/classifications">Classifications</Link></li>
          </ul>}
          <div style={{display: click ? "block" : "none"}}  className='n-ico'>
           <i onClick={()=>setProduct(!product)} style={{cursor: 'pointer'}} className="fa-solid fa-chevron-right"></i>
          </div>
        </li>

        <li className='n-main n-left'>
        <Link to='/shipments' style={{color: "#144339"}}>
          <div className='main-icone'>
           <i className="fa-solid fa-truck-fast"></i>
            <span  style={{display: click ? "block" : "none"}}>Shipments</span>
          </div>
         </Link>
      { product2 && <ul className="dropDown dropDown2">
          <li className="dropDownPath"><Link to="/customer">Customer</Link></li>
         </ul>}
          <div style={{display: click ? "block" : "none"}}  className='n-ico'>
           <i onClick={()=>setProduct2(!product2)} style={{cursor: 'pointer'}} className="fa-solid fa-chevron-right"></i>
          </div>
        </li>

        <li className='n-main n-left'>
        <Link to='/wallet'>
         <div className='main-icone'>
          <i className="fa-solid fa-wallet"></i>
           <span  style={{display: click ? "block" : "none" , color: "#144339"}}>Wallet</span>
         </div>
         </Link>
     { product3 && <ul className="dropDown">
         <li className="dropDownPath"><Link to="/bankAccounts">Bank Accounts</Link></li>
         <li className="dropDownPath"><Link to="/paymentMethods">PaymentMethods</Link></li>
        </ul>}
         <div style={{display: click ? "block" : "none"}}  className='n-ico'>
          <i onClick={()=>setProduct3(!product3)} style={{cursor: 'pointer'}} className="fa-solid fa-chevron-right"></i>
         </div>
        </li>

        <Link to='/mybills' name="My Bills" className='nav-links'  onClick={closeMobileMenu}>
         <li className='n-main'>
          <i className="fa-solid fa-money-bill"></i>
           <span  style={{display: click ? "block" : "none"}}>My Bills</span>
         </li>
        </Link>

         <Link to='/settings' name="Settings" className='nav-links'  onClick={closeMobileMenu}>
          <li className='n-main'>
           <i className="fa-solid fa-sliders"></i>
           <span  style={{display: click ? "block" : "none"}}>Settings</span>
          </li>
         </Link>

         </div>
         <Link to='/signOut' name="Sign out" className='nav-links'  onClick={closeMobileMenu}>
           <li className='n-main n-but'>
            <i className="fa-solid fa-arrow-right-from-bracket"></i>
           <span  style={{display: click ? "block" : "none"}}>Sign out</span>
           </li>
         </Link>

        </div>
       </div>
       </ul>
       <main>{Children}</main>
    </navbar>
)
}

export default Headers;